import React, { useEffect, useRef, useState } from "react";
import {
  Button,
  ButtonGroup,
  Col,
  Form,
  InputGroup,
  Row,
} from "react-bootstrap";
import DataTable from "react-data-table-component";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import SelectionBox from "../../components/UI/Form/SelectionBox";
import LoadingData from "../../components/UI/LoadingData";
import UiModal from "../../components/UI/UiModal";
import {
  ajaxCallWithHeaderOnly,
  ajaxCallWithoutBody,
} from "../../helpers/ajaxCall";
import { uiAction } from "../../store/uiStore";
import EnquiryColumnFilter from "./EnquiryColumnFilter";
import ColumnFilter from "../../components/Filter/ColumnFilter";
import EnquiryFilter from "./EnquiryFilter";
import CommentPopup from "../../components/enq/CommentPopup";
import DupEnq from "../../components/enq/DupEnq";
import ChangeAssignUser from "../../components/enq/ChangeAssignUser";
const handleButtonClick = (data) => {
  // console.log(data);
};
const allColumns = [
  { name: "Student Name", id: "name" },
  { name: "Student Phone", id: "phone" },
  { name: "Student Email", id: "email" },
  { name: "Current Education", id: "current_edu" },
  { name: "Country Interested", id: "country_interested" },
  { name: "University Interested", id: "university_interested" },
  { name: "Course Interested", id: "course_interested" },
  { name: "Level Applying For", id: "level_applying_for" },
  { name: "Intake Interested", id: "intake_interested" },
  { name: "Assigned Users", id: "assigned_user" },
  { name: "Enquiry Status", id: "enq_status" },
  { name: "Added By", id: "added_by" },
  { name: "Notes", id: "notes" },
];
function Enquiries() {
  const [enqData, setEnqData] = useState([]);
  const [allEnq, setAllEnq] = useState(true);
  const [refreshNeeded, setRefresherNeeded] = useState(true);
  const [assignUsrData, setAssignUsrData] = useState([]);
  const [enqFilter, setEnqFilter] = useState({
    university_interested: null,
    level_applying_for: null,
    intake_interested: null,
    enquiry_status: null,
    assigned_usr: null,
  });

  // data table code starts
  const [pageNo, setPageNo] = useState(1);
  const [perPage, setPerPage] = useState(10);
  const [totalRows, setTotalRows] = useState(0);
  // const [resetPagination, setResetPagination] = useState(false);
  const lastDataCount = useRef(0);
  const [showCommentPopup, setShowCommentPopup] = useState({
    show: false,
    enqId: null,
    name: null,
  });
  // console.log("page no and per page data is", pageNo, perPage);

  // const shouldPaginationReset = function () {
  //   if (resetPagination) {
  //     setResetPagination(false);
  //     return true;
  //   }
  //   return false;
  // };
  const assignedUserFilter = () => {
    setAllEnq((status) => !status);
    setRefresherNeeded(true);
  };

  const handlePerRowsChange = (newPerPage, page) => {
    // console.log("per row is changed and data is", newPerPage, page);
    setPerPage(newPerPage);
    setPageNo(page);
    setEnqData([]);
    setRefresherNeeded(true);
  };
  // data table code ended

  // normal vars to set the student Name and id
  const deleteEntryDetails = useRef({});
  const [searchText, setSearchText] = useState("");
  const [isLoadingData, setIsLoadingData] = useState(false);
  const [promptStatus, setPromptStatus] = useState(false);
  const authData = useSelector((state) => state.authStore);
  const navigate = useNavigate();
  const [throwErr, setThrowErr] = useState(null);
  const columnData = useSelector((store) => store.enqColumn);
  const dispatch = useDispatch();
  const promptDelete = (student_name, deleteId) => {
    setPromptStatus(true);
    deleteEntryDetails.current = { name: student_name, id: deleteId };
  };
  const deleteEnquiry = async function (deleteId) {
    try {
      setSearchText("");
      setIsLoadingData(true);
      const response = await ajaxCallWithoutBody(
        `update-enquiry/${deleteId}/`,
        {
          Authorization: `Bearer ${authData.accessToken}`,
        },
        "DELETE"
      );

      // console.log("response is ", response);
      if (response !== true) {
        setThrowErr({ ...response, page: "enquiries" });
        return;
      }
      dispatch(
        uiAction.setNotification({
          show: true,
          heading: "Enquiry Deleted Successfully",
          msg: `<strong>${deleteEntryDetails.current.name}</strong> enquiry Deleted successfully`,
        })
      );
      deleteEntryDetails.current = {};
      setEnqData([]);
      setRefresherNeeded(true);
    } catch (e) {
      setThrowErr({ e, page: "enquiries" });
      return;
    }
  };

  const columnsNormal = [
    {
      cell: (row) => (
        <>
          <DupEnq data={row} refresh={setRefresherNeeded} />
          <button
            className="enquiryAction"
            title="Edit Enquiry"
            onClick={() => {
              navigate(`/enquiry/edit/${row.id}`);
            }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
              class="feather feather-edit-2 p-1 br-8 mb-1"
            >
              <path d="M17 3a2.828 2.828 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3z"></path>
            </svg>
          </button>
          <button
            className="enquiryAction"
            title="Delete Enquiry"
            onClick={() => {
              promptDelete(row.student_name, row.id);
              // deleteEnquiry(row.id);
            }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
              class="feather feather-trash p-1 br-8 mb-1"
            >
              <polyline points="3 6 5 6 21 6"></polyline>
              <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
            </svg>
          </button>
        </>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },

    {
      name: "Student Name",
      selector: (row) => row.student_name,
      sortable: true,
      omit: columnData.name,
    },

    {
      name: "Assigned Users",
      selector: (row) => (
        <ChangeAssignUser
          enqName={row.student_name}
          allUser={assignUsrData}
          name={row.assigned_users}
          assignId={row.assigned_usersId}
          enqId={row.id}
        />
      ),
      omit: columnData.assigned_user,
    },
    {
      name: "Student Phone",
      selector: (row) => row.student_phone,
      omit: columnData.phone,
    },
    {
      name: "Student Email",
      selector: (row) => row.student_email,
      grow: 1,
      omit: columnData.email,
    },
    {
      name: "Current Education",
      selector: (row) => row.current_education,
      omit: columnData.current_edu,
    },
    {
      name: "Country Interested",
      selector: (row) => row.country_interested,
      omit: columnData.country_interested,
    },
    {
      name: "University Interested",
      selector: (row) => row.university_interested,
      omit: columnData.university_interested,
    },
    {
      name: "Course Interested",
      selector: (row) => row.course_interested,
      omit: columnData.course_interested,
    },
    {
      name: "Level Applying For",
      selector: (row) => row.level_applying_for,
      omit: columnData.level_applying_for,
    },
    {
      name: "Intake Interested",
      selector: (row) => row.intake_interested,
      omit: columnData.intake_interested,
    },

    {
      name: "Enquiry Status",
      selector: (row) => row.enquiry_status,
      omit: columnData.enq_status,
    },
    {
      name: "Enq Date",
      selector: (row) => row.date_created,
      omit: columnData.date,
    },
    {
      name: "Added By",
      selector: (row) => row.added_by,
      omit: columnData.added_by,
    },
    {
      name: "Notes",
      selector: (row) => row.notes,
      omit: columnData.notes,
    },
    {
      name: "Comments",
      selector: (row) => (
        <p
          className="pointer"
          onClick={() => showComments(row.id, row.student_name)}
        >
          check
        </p>
      ),
      omit: columnData.comments,
    },
  ];

  const columns = columnsNormal.filter((column) => {
    if (column?.name === "Assigned Users") {
      return authData.user_type === "superuser";
    }
    return true;
  });
  useEffect(() => {
    if (throwErr) throw throwErr;
  }, [throwErr]);

  const getAssignUsrData = async function () {
    const response = await ajaxCallWithHeaderOnly("userlist", {
      Authorization: `Bearer ${authData.accessToken}`,
    });
    if (response?.isNetwork) {
      setThrowErr({ ...response, page: "enquiries" });
      return;
    }
    if (response?.status === 401) {
      setThrowErr({ ...response, page: "enquiries" });
      return;
    }
    if (!response?.length) {
      setThrowErr({ ...response, page: "enquiries" });
      return;
    }
    setAssignUsrData((data) => {
      return response.map((option) => {
        return { value: option.id, name: option.username };
      });
    });
  };
  useEffect(() => {
    if (enqData.length && !assignUsrData.length) {
      getAssignUsrData();
    }
  }, [enqData]);

  const getEnqDTData = async (url) => {
    setIsLoadingData(true);
    const response = await ajaxCallWithHeaderOnly(
      url,
      {
        Authorization: `Bearer ${authData.accessToken}`,
      },
      "GET",
      null
    );
    if (response?.isNetwork) {
      setThrowErr({ ...response, page: "enquiries" });
      return;
    }
    if (response?.status === 401) {
      setThrowErr({ ...response, page: "enquiries" });
      return;
    }
    // console.log(response);

    if (response?.results?.length > 0) {
      const data = response.results.map((data) => {
        return {
          ...data,
          date_created: data?.date_created.split("-").reverse().join("-"),
          added_by: data?.added_by?.username,
          intake_interested: data?.intake_interested?.intake_month
            ? `${data?.intake_interested?.intake_month} ${data?.intake_interested?.intake_year}`
            : "",
          assigned_users: data?.assigned_users?.username
            ? data?.assigned_users?.username
            : "-",
          assigned_usersId: data?.assigned_users?.id,
          country_interested: data?.country_interested?.country_name,
          course_interested: data?.course_interested?.course_name,
          current_education: data?.current_education?.current_education,
          current_educationId: data?.current_education?.id,
          enquiry_status: data?.enquiry_status?.status,
          level_applying_for: data?.level_applying_for?.levels,
          university_interested: data?.university_interested?.univ_name,
        };
      });
      if (data?.length) setEnqData(data);
      setTotalRows(response.count);
    } else {
      setEnqData([]);
    }
    setIsLoadingData(false);
    setRefresherNeeded(false);
    // setResetPagination(true);
  };
  useEffect(() => {
    if (refreshNeeded) {
      let url = `enquiries/?ordering=-date_created&p=${pageNo}&records=${perPage}`;
      // if (
      //   !(
      //     enqFilter.university_interested &&
      //     enqFilter.level_applying_for &&
      //     enqFilter.intake_interested &&
      //     enqFilter.enquiry_status
      //   )
      // ) {
      //   // console.log(" hey there here too");
      //   clearFilter();
      // }
      url += enqFilter.university_interested
        ? `&university_interested=${enqFilter.university_interested}`
        : "";
      url += enqFilter.level_applying_for
        ? `&level_applying_for=${enqFilter.level_applying_for}`
        : "";
      url += enqFilter.intake_interested
        ? `&intake_interested=${enqFilter.intake_interested}`
        : "";
      url += enqFilter.enquiry_status
        ? `&enquiry_status=${enqFilter.enquiry_status}`
        : "";
      url += enqFilter.assigned_usr
        ? `&assigned_users=${enqFilter.assigned_usr}`
        : "";
      url +=
        authData.user_type === "superuser"
          ? !allEnq
            ? "&assigned_usersf=1"
            : ""
          : "";
      try {
        getEnqDTData(url);
      } catch (e) {
        setThrowErr({ e, page: "enquiries" });
        return;
      }
      setRefresherNeeded(false);
    }
  }, [enqFilter, refreshNeeded, allEnq, pageNo, perPage]);
  const handleChange = ({ selectedRows }) => {
    // You can set state or dispatch with something like Redux so we can use the retrieved data
    // console.log("Selected Rows: ", selectedRows);
  };
  // const data = enqData;
  const filterSelectionChanged = function (filterName, val) {
    setEnqFilter((oldFilter) => {
      // copying object
      const filterData = JSON.parse(JSON.stringify(oldFilter));
      filterData[filterName] = val;
      // console.log("filter data is ", filterData);
      return filterData;
    });
    setRefresherNeeded(true);
    setPageNo(1);
    // setEnqFilter] = useState({
    //   university_interested: null,
    //   level_applying_for: null,
    //   intake_interested: null,
  };

  const searchEnq = (e) => {
    try {
      e.preventDefault();
      if (searchText?.length) {
        const partURL =
          authData.user_type === "superuser"
            ? !allEnq
              ? "&assigned_usersf=1"
              : ""
            : "";
        setIsLoadingData(true);
        setPageNo(1);
        let url = `enquiries/?ordering=-date_created&search=${searchText}${partURL}`;
        getEnqDTData(url);
      }
    } catch (e) {
      setThrowErr({ e, page: "enquiries" });
      return;
    }
  };

  const clearFilter = function () {
    setEnqFilter({
      university_interested: null,
      level_applying_for: null,
      intake_interested: null,
      enquiry_status: null,
      assigned_usr: null,
    });
    setEnqData([]);
    setPageNo(1);
    setRefresherNeeded(true);
  };
  const clearSearch = function () {
    if (searchText?.length) {
      setEnqData([]);
      setRefresherNeeded(true);
      setPageNo(1);
    }
    setSearchText("");
  };

  // to show the comment popup
  const showComments = function (enqId, name) {
    setShowCommentPopup({ show: true, enqId, name });
  };
  return (
    <>
      <div className="row layout-spacing">
        {/* <ColumnFilter
          allColumns={allColumns}
          enqFilter={enqFilter}
          filterOnClick={clearFilter}
          showFilterBtn={
            enqFilter.university_interested ||
            enqFilter.level_applying_for ||
            enqFilter.intake_interested ||
            enqFilter.enquiry_status
          }
        /> */}
        <div className="neumorphism-box nmb">
          <div className="col-lg-12">
            <div className="row">
              <EnquiryFilter
                isAdmin={authData.user_type === "superuser"}
                filterSelectionChanged={filterSelectionChanged}
                enqFilter={enqFilter}
                searchText={searchText}
                setSearchText={setSearchText}
                clearSearch={clearSearch}
                searchEnq={searchEnq}
              />
            </div>
          </div>
        </div>
        <div className="neumorphism-box">
          <div className="col-lg-12">
            <div className="statbox widget box box-shadow">
              <div className="widget-content widget-content-area">
                <EnquiryColumnFilter
                  showFilterBtn={false}
                  filterStore="enqColumn"
                  enqFilter={enqFilter}
                  filterOnClick={clearFilter}
                />
                {authData.user_type === "superuser" ? (
                  <div className="text-center mb-3">
                    <Button
                      variant={`${allEnq ? "primary" : "outline-primary"}`}
                      className="mr-3"
                      onClick={assignedUserFilter}
                    >
                      All Enqiry
                    </Button>
                    <Button
                      variant={`${allEnq ? "outline-primary" : "primary"}`}
                      onClick={assignedUserFilter}
                    >
                      Enquiry With No Assigned User
                    </Button>
                  </div>
                ) : (
                  ""
                )}

                <DataTable
                  autoResetPage={true}
                  onChangePage={(page) => {
                    // console.log("new Page numbner is", page);
                    setPageNo(page);
                    setEnqData([]);
                    setRefresherNeeded(true);
                  }}
                  columns={columns}
                  data={enqData}
                  onChangeRowsPerPage={handlePerRowsChange}
                  selectableRows
                  onSelectedRowsChange={handleChange}
                  pagination
                  paginationServer
                  progressPending={isLoadingData}
                  progressComponent={
                    <LoadingData className="loading-spinner-flex" />
                  }
                  paginationTotalRows={totalRows}
                  key={totalRows}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      {promptStatus ? (
        <UiModal
          setModalStatus={() => {
            setPromptStatus(false);
          }}
          showStatus={promptStatus}
          showHeader={false}
          body={
            <div className="delete-modal text-center">
              <div className="delete-modal__icon">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="80"
                  height="80"
                  viewBox="0 0 24 24"
                >
                  <path
                    fill="#f15e5e"
                    d="M8.27 3L3 8.27v7.46L8.27 21h7.46C17.5 19.24 21 15.73 21 15.73V8.27L15.73 3M9.1 5h5.8L19 9.1v5.8L14.9 19H9.1L5 14.9V9.1m6 5.9h2v2h-2v-2m0-8h2v6h-2V7"
                  />
                </svg>
              </div>
              <div className="delete-modal__text">
                <h3>Are you sure?</h3>
                <p>
                  Do you really want to delete
                  <strong> {deleteEntryDetails.current.name} ?</strong>
                </p>
              </div>
              <div className="delete-modal__action">
                <button
                  class="btn btn-light-dark mb-2 me-4"
                  onClick={() => {
                    setPromptStatus(false);
                  }}
                >
                  Cancel
                </button>
                <button
                  class="btn btn-danger mb-2 me-4"
                  onClick={() => {
                    deleteEnquiry(deleteEntryDetails.current.id);
                    setPromptStatus(false);
                  }}
                >
                  Delete
                </button>
              </div>
            </div>
          }
          showFooter={false}
          footerContent=""
        />
      ) : (
        ""
      )}
      {showCommentPopup.show ? (
        <CommentPopup
          id={showCommentPopup.enqId}
          title={showCommentPopup.name}
          onHide={() => {
            setShowCommentPopup({ show: false, enqId: null, name: null });
          }}
        />
      ) : (
        ""
      )}
    </>
  );
}

export default Enquiries;
