import React, { useEffect, useState } from "react";
import { Button, Form, Popover } from "react-bootstrap";
import { FileUploader } from "react-drag-drop-files";
import { ajaxCall } from "../../helpers/ajaxCall";
import { useDispatch, useSelector } from "react-redux";
import { uiAction } from "../../store/uiStore";
import UiModal from "../UI/UiModal";
import LoadingData from "../UI/LoadingData";

const UploadOfferLetter = (props) => {
  const [uploading, setUploading] = useState(false);
  // console.log("id is", id);
  const authData = useSelector((state) => state.authStore);
  const dispatch = useDispatch();
  const [throwErr, setThrowErr] = useState(null);
  useEffect(() => {
    if (throwErr) throw throwErr;
  }, [throwErr]);
  const uploadFile = async function (id, file) {
    const fdata = new FormData();

    if (file instanceof File) fdata.append("rcvd_offer_letter", file);
    setUploading(true);
    const response = await ajaxCall(
      `update-application/${id}/`,
      {
        Authorization: `Bearer ${authData.accessToken}`,
      },
      "PATCH",
      fdata
    );
    if (response?.isNetwork) {
      setThrowErr({ ...response, page: "enqForm" });
      return;
    }
    if (response?.status === 401 || response?.status === 204) {
      setThrowErr({ ...response, page: "enqForm" });
      return;
    }
    if (response?.status === 400) {
      // setLoadError({ isError: "Please check all form fields and try again" });
      dispatch(
        uiAction.setNotification({
          show: true,
          heading: "Application",
          msg: `Problem Occured while uploading Offer Letter, Please try again`,
        })
      );
      return;
    }
    // setLoadError({ isError: "Please check all form fields and try again" });
    dispatch(
      uiAction.setNotification({
        show: true,
        heading: "Application",
        msg: `Offer Letter Uploaded Successfully for ${props.name} `,
      })
    );
    props.resetApp();
    closeModal();
    setUploading(false);
  };

  const closeModal = () => {
    props.changeMode({ show: false, id: null, name: "" });
  };
  return (
    <>
      <UiModal
        setModalStatus={closeModal}
        showStatus={true}
        showHeader={true}
        title={`Upload Offer Letter for ${props.name}`}
        body={
          uploading ? (
            <div className="text-center">Uploading</div>
          ) : (
            <>
              <div className="flexCenter">
                <FileUploader
                  handleChange={(file) => {
                    uploadFile(props.id, file);
                  }}
                  // name={props.fieldName}
                  // types={fileTypes}
                  hoverTitle="Upload Document"
                  minSize={0.1}
                  maxSize={4}
                />
              </div>
              <div className="text-center mt-3">
                <Button variant="danjor-outline" onClick={closeModal}>
                  Cancel Upload
                </Button>
              </div>
            </>
          )
        }
        //   showFooter={modalStatus.showFooter}
        //   footerContent={modalStatus.footerContent}
        //
      />
    </>
  );
};

export default UploadOfferLetter;
